import React, { useEffect, useRef, useState } from 'react';
import { Button, IconButton, List, ListItem, ListItemIcon, ListItemText, TextField, Typography } from '@mui/material'
import { LocalizationProvider, DatePicker } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fi } from 'date-fns/locale';
import { v4 as uuid4} from 'uuid';
import { Link } from 'react-router-dom';
import { format } from 'date-fns';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';

interface Props{
  tulot : Tulo[], 
  setTulot : (arg0 : Tulo[]) => void;
}

const LisaaTulo : React.FC<Props> = ({tulot, setTulot}) : React.ReactElement => {
  
  const [pvm, setPvm] = useState<Date>(new Date());
  const [summaOk, setSummaOk] = useState<boolean>();
  const [uusiTulo, setUusiTulo] = useState<string>("")

  const uusiTuloRef : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();

  useEffect( () : void => {

    if (Number.isInteger(Number(uusiTulo)) === false || Number(uusiTulo) < 100) {
    setSummaOk(false)
    } else {
      setSummaOk(true)
    }

 }, [uusiTulo]);

  const lisaaTulo = () : void => {

    let apuTulo : Tulo = {
      id: uuid4(),
      pvm: pvm,
      eur: Number(uusiTuloRef.current!.value)
      }

    setTulot([...tulot, apuTulo]);

    }

  return (
   <> 
   <Typography variant='h6' sx={{marginTop: "20px"}}>Lis tulo</Typography>
   <Typography sx={{marginTop: "20px"}}>Tll voit list ja tarkastella tulojasi. Mikli et muuta pivmr, tallentuu tulo kuluvalle pivlle.</Typography>
   <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
   <div style={{display: "flex", justifyContent: "space-between", marginTop: "20px"}}>
    <DatePicker
            sx={{marginBottom: "10px"}}
            label="Pivmr"
            value={pvm}
            onChange={(uusiAika : Date | null) => setPvm(uusiAika!)}         
        />
    <TextField 
            label="Summa "
            inputRef={uusiTuloRef}
            variant="outlined"
            required={true}
            onChange={(e) => setUusiTulo(e.target.value)}
            value={uusiTulo}
            error={!summaOk}
            helperText={!summaOk ? "Syt kokonaisluku. Minimi 100." : ""}
            sx={{marginBottom : "10px"}}
        />  
   </div>
   <Button
            sx={{}}
            fullWidth={true}
            variant='contained'
            disabled={!summaOk}
            onClick={() =>
              lisaaTulo()}
            >Lis

    </Button>
    <Button
                fullWidth={true}
                component={Link}
                to="/"
            >Takaisin aloitussivulle</Button> 
    </LocalizationProvider>
    
    <Typography variant='h6' sx={{marginTop: "20px"}}>Tulosi aikajrjestyksess</Typography>

    <List>
    {tulot.sort((a, b) => b.pvm.getTime() - a.pvm.getTime()).map( (tulo : Tulo, idx : number) => {

        return (
            <ListItem key={idx}>
                <ListItemText>{tulo.eur} eur</ListItemText>
                <ListItemText>{format(tulo.pvm, "d.M.Y")}</ListItemText>
                <ListItemIcon>
                    <IconButton 
                        component={Link}
                        to={`/muokkaaTulo/${tulo.id}`}
                        edge="end"
                    >
                        <EditIcon />
                    </IconButton>
                    </ListItemIcon>
                <ListItemIcon>
                    <IconButton 
                        component={Link}                                
                        to={`/poistaTulo/${tulo.id}`}
                        edge="end"
                    >
                        <DeleteIcon />
                    </IconButton>
                    </ListItemIcon>
            </ListItem>
            );
         } ) }
    </List>
   </>
  );
}

export default LisaaTulo;