import React from 'react'
import { Button, Typography } from '@mui/material';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';
import { format } from 'date-fns';

interface Props {
    tulot : Tulo[]
    setTulot : (arg0 : Tulo[]) => void
}

const PoistaTulo : React.FC<Props> = ({ tulot, setTulot }) : React.ReactElement => {

    const navigate : NavigateFunction = useNavigate();

    const { id } = useParams<any>();

    const poistettavaTulo : Tulo| undefined = tulot.find((tulo: Tulo) => {
        return tulo.id === id;
    });

    const vahvistaPoisto = () : void => {
    
        setTulot([...tulot.filter((tulo : Tulo) => tulo.id !== id)]);

        navigate("/LisaaTulo");
    }

    return (
    <>
        <Typography variant='h6' sx={{marginTop: "20px", marginBottom : "10px;"}}>Poiston vahvistus</Typography>

        <Typography
          sx={{marginBottom : "10px", marginTop: "20px", fontStyle: "italic"}}
      >Haluatko varmasti poistaa seuraavan tulon: {poistettavaTulo!.eur} eur, {format(poistettavaTulo!.pvm, "d.M.Y")}?</Typography>
        

        <Button
            variant="contained"
            fullWidth={true}
            onClick={vahvistaPoisto}
        >Poista tulo</Button>  

        <Button
            fullWidth={true}
            component={Link}
            to="/"
        >Peruuta</Button>    
    </>
    )
}

export default PoistaTulo;