import React, { useEffect, useRef, useState } from 'react';
import { Button, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent, TextField, Typography } from '@mui/material'
import { LocalizationProvider, DatePicker } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fi } from 'date-fns/locale';
import { v4 as uuid4} from 'uuid';
import { Link, NavigateFunction, useNavigate } from 'react-router-dom';


interface Props{
  menot : Meno[], 
  setMenot : (arg0 : Meno[]) => void;
  menoKategoriat : string[];
}

const LisaaMeno : React.FC<Props> = ({menot, setMenot, menoKategoriat}) : React.ReactElement => {
  
  const [pvm, setPvm] = useState<Date>(new Date());
  const [summaOk, setSummaOk] = useState<boolean>();
  const [uusiMeno, setUusiMeno] = useState<string>("")
  const [kateg, setKateg] = useState<string>("")
  const [tiedotOk, setTiedotOk] = useState<boolean>(false)
  const [katSyotetty, setKatSyotetty] = useState<boolean>(false);

  const uusiMenoRef : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();

  const navigate : NavigateFunction = useNavigate();

  useEffect( () : void => {
    
    if (Number.isInteger(Number(uusiMeno)) === false || (Number(uusiMeno)) < 1 ) {
        setSummaOk(false)
        } else {
            setSummaOk(true)
            }
    
            if ( kateg === "" ) {
    setKatSyotetty(false)
    } else {
        setKatSyotetty(true)
    }

    if(summaOk === false || katSyotetty === false) {
        setTiedotOk(false)
    } else {
        setTiedotOk(true)
    }

    }, [uusiMeno, kateg, summaOk, katSyotetty]); 

  const lisaaMeno = () : void => {

    let apuMeno : Meno = {
      id: uuid4(),
      pvm: pvm,
      kat: kateg,
      eur: Number(uusiMenoRef.current!.value)
      }

    setMenot([...menot, apuMeno]);

    navigate("/");


    }

  return (
   <> 

   <Typography variant='h6' sx={{marginTop: "20px"}}>Lis kulu</Typography>

   <Typography sx={{marginTop: "20px"}}>Tll voit list kulun. Kategoria ja summa ovat pakollisia. Mikli et muuta pivmr, tallentuu kulu kuluvalle pivlle.</Typography>

   <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
  
   <FormControl
      sx={{marginBottom: "10px", marginTop: "20px"}}
      variant="outlined" 
      fullWidth
    >
      <InputLabel
        style={{backgroundColor: "#fff",
                paddingLeft: 5,
                paddingRight: 5}}
      >Kategoria</InputLabel>

      <Select
        value={kateg}
        required={true}
        error={!katSyotetty}
        displayEmpty
        onChange={(e : SelectChangeEvent) => { setKateg(e.target.value) }}
      >
        {menoKategoriat.sort().map((kategoria : string, idx : number) => {
          return <MenuItem key={idx} value={kategoria}>{kategoria}</MenuItem>
        })}   
      </Select>
    </FormControl> 

   <div style={{display: "flex", justifyContent: "space-between"}}>
    <DatePicker
            sx={{marginBottom: "10px"}}
            label="Pivmr"
            value={pvm}
            onChange={(uusiAika : Date | null) => setPvm(uusiAika!)}         
        />
    <TextField 
            label="Summa "
            inputRef={uusiMenoRef}
            variant="outlined"
            required={true}
            onChange={(e) => setUusiMeno(e.target.value)}
            value={uusiMeno}
            error={!summaOk}
            helperText={!summaOk ? "Syt kokonaisluku." : ""}
            sx={{marginBottom : "10px"}}
        />  
   </div>
   <Button
            sx={{}}
            fullWidth={true}
            variant='contained'
            disabled={!tiedotOk}
            onClick={() =>
              lisaaMeno()}
            >Lis

    </Button>

    <Button
                fullWidth={true}
                component={Link}
                to="/"
            >Takaisin aloitussivulle</Button> 
    </LocalizationProvider>
   </>
  );
}

export default LisaaMeno;