import React, { useState } from 'react'
import { Button, Card, Typography } from '@mui/material';
import { Link } from 'react-router-dom';
import { addDays, isWithinInterval } from 'date-fns';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { LocalizationProvider, DatePicker } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fi } from 'date-fns/locale';


interface Props {
    tulot : Tulo[]
    setTulot : (arg0 : Tulo[]) => void
    menot : Meno[]
    setMenot : (arg0 : Meno[]) => void
    menoKategoriat : string[]
}

const Yhteenveto : React.FC<Props> = ({ tulot, setTulot, menot, setMenot, menoKategoriat }) : React.ReactElement => {

    const [alku, setAlku] = useState<Date>();
    const [loppu, setLoppu] = useState<Date>();
   
    return (
    <>
        <Typography variant='h5' sx={{marginBottom : "20px;", marginTop: "20px"}}>Yhteenveto</Typography>
        <Typography sx={{marginBottom : "20px;", marginTop: "20px"}}>Oletuksena yhteenveto nytt kaiken syttmsi tiedon. Voit suodattaa halutessasi tarkastelujaksoa alla. </Typography>
        
        <Typography variant='h6' sx={{marginTop: "20px", marginBottom: "10px"}}>Valitse tarkasteltava aikajakso</Typography>

        <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
        <div style={{display:"flex", justifyContent: "space-between"}}>
        <DatePicker
            sx={{marginBottom: "10px"}}
            label="Alku" 
            maxDate={new Date()}
            value={alku}
            onChange={(uusiAika : Date | null) => setAlku(uusiAika!)}         
        />
        <DatePicker
            sx={{marginBottom: "10px"}}
            label="Loppu"
            minDate={addDays(alku!,1)}
            maxDate={new Date()}
            value={loppu}
            onChange={(uusiAika : Date | null) => setLoppu(uusiAika!)}    
        />  
        </div>

         <Button
            sx={{marginBottom: "10px"}}
            fullWidth={true}
            variant='contained'
            onClick={() => window.location.reload()}
        >Tyhjenn suodatus</Button>

        <Button
            sx={{marginBottom: "10px"}}
            fullWidth={true}
            component={Link}
            to="/"
        >takaisin aloitussivulle</Button> 
        <Typography variant='h6' sx={{marginTop: "20px", marginBottom: "10px"}}>Taloutesi kokonaisyhteenveto</Typography>

        <TableContainer component={Card} sx={{marginBottom:"20px"}}>
            <TableHead>
                <TableRow>
                    <TableCell>Tulot</TableCell>
                    <TableCell align="right">Menot</TableCell>
                    <TableCell align="right">Sst</TableCell>
                </TableRow>
            </TableHead>

            {(!alku || !loppu)
            ?<TableBody>
            <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
                <TableCell align='right'>
                    {tulot.reduce((laskuri: number, tulo: Tulo) => {
                        return(laskuri + tulo.eur);
                    },0)}
                </TableCell>
                <TableCell align='right'>
                    {menot.reduce((laskuri: number, meno: Meno) => {
                        return(laskuri + meno.eur);
                    },0)}
                </TableCell>
                <TableCell align='right'>
                    {tulot.reduce((laskuri: number, tulo: Tulo) => {
                        return(laskuri + tulo.eur);
                    },0) - menot.reduce((laskuri: number, meno: Meno) => {
                        return(laskuri + meno.eur);
                    },0)}
                </TableCell>
            </TableRow>
        </TableBody>
            :<TableBody>
                <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                    <TableCell align='right'>
                        {tulot.filter((tulo : Tulo) => isWithinInterval(tulo.pvm,{
                        start: alku,
                        end: addDays(loppu, 1)
                        })).reduce((laskuri: number, tulo: Tulo) => {
                            return(laskuri + tulo.eur);
                        },0)}
                    </TableCell>
                    <TableCell align='right'>
                        {menot.filter((meno : Meno) => isWithinInterval(meno.pvm,{
                        start: alku,
                        end: addDays(loppu, 1)
                    })).reduce((laskuri: number, meno: Meno) => {
                            return(laskuri + meno.eur);
                        },0)}
                    </TableCell>
                    <TableCell align='right'>
                    {tulot.filter((tulo : Tulo) => isWithinInterval(tulo.pvm,{
                        start: alku,
                        end: addDays(loppu, 1)
                        })).reduce((laskuri: number, tulo: Tulo) => {
                            return(laskuri + tulo.eur);
                        },0) - menot.filter((meno : Meno) => isWithinInterval(meno.pvm,{
                            start: alku,
                            end: addDays(loppu, 1)
                        })).reduce((laskuri: number, meno: Meno) => {
                                return(laskuri + meno.eur);
                            },0)}
                    </TableCell>
                </TableRow>
            </TableBody>
            }

        </TableContainer>

        <Typography variant='h6' sx={{marginTop: "20px", marginBottom:"10px"}}>Kulutuksesi yhteenveto kategorioittain</Typography>
        
        <TableContainer component={Paper} sx={{}}>
            <TableHead>
                <TableRow>
                    <TableCell>Kategoria</TableCell>
                    <TableCell align="right">Yhteens</TableCell>
                </TableRow>
            </TableHead>
            {(!alku || !loppu)
            ?<TableBody>
            {menoKategoriat.map((kategoria: string, idx: number) => {
                return <TableRow
                    key={idx}
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                    <TableCell component="th" scope="row">{kategoria}</TableCell>
                    <TableCell align="right">
                        {menot.filter((meno : Meno) => meno.kat === kategoria).reduce((laskuri: number, meno : Meno) => {
                            return(laskuri + meno.eur);
                        },0)}
                    </TableCell>
                </TableRow>
                })}
                <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                    <TableCell component="th" scope="row">Yhteens</TableCell>
                    <TableCell align="right">
                        {menot.reduce((laskuri: number, meno : Meno) => {
                            return(laskuri + meno.eur);
                        },0)}
                    </TableCell>
                </TableRow>
                    
            </TableBody>
            :<TableBody>
            {menoKategoriat.map((kategoria: string, idx: number) => {
                return <TableRow
                    key={idx}
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                    <TableCell component="th" scope="row">{kategoria}</TableCell>
                    <TableCell align="right">
                    {menot.filter((meno : Meno) => 
                    meno.kat === kategoria && isWithinInterval(meno.pvm,{
                        start: alku,
                        end: addDays(loppu, 1)
                    })).reduce((laskuri: number, meno : Meno) => {
                        return(laskuri + meno.eur);
                    },0)}
                    </TableCell>
                </TableRow>
                })}
                <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                    <TableCell component="th" scope="row">Yhteens</TableCell>
                    <TableCell align='right'>
                    {menot.filter((meno : Meno) => 
                    isWithinInterval(meno.pvm,{
                        start: alku,
                        end: addDays(loppu, 1)
                    })).reduce((laskuri: number, meno : Meno) => {
                        return(laskuri + meno.eur);
                    },0)}
                    </TableCell>
                </TableRow>
            </TableBody>
            }   
        </TableContainer>

        </LocalizationProvider>   
    </>
    )
}

export default Yhteenveto;