import React, { useEffect, useRef, useState } from 'react';
import { Container } from '@mui/material';
import Header from './components/Header';
import LisaaTulo from './components/LisaaTulo';
import Aloitus from './components/Aloitus';
import { Routes, Route } from 'react-router-dom';
import MuokkaaMeno from './components/MuokkaaMeno';
import PoistaMeno from './components/PoistaMeno';
import MuokkaaTulo from './components/MuokkaaTulo';
import PoistaTulo from './components/PoistaTulo';
import Yhteenveto from './components/Yhteenveto';
import LisaaMeno from './components/LisaaMeno';

const App : React.FC = () : React.ReactElement => {

  const [tulot, setTulot] = useState<Tulo[]>([]);
  const [menot, setMenot] = useState<Meno[]>([]);
  const [menoKategoriat] = useState<string[]>([
    "Asuminen",
    "Ruoka",
    "Hygienia",
    "Terveys",
    "Matkat",
    "Vakuutukset",
    "Harrastukset",
    "Kahvilat- ja ravintolat",
    "Vaatteet, kengt, asusteet",
    "Muu"
  ])
 

  const kaynnistetty : React.MutableRefObject<boolean> = useRef(false);

    useEffect(() => {
      
      if (!kaynnistetty.current)  {
        
        if (localStorage.getItem("menolista")) {
         
          setMenot(JSON.parse(String(localStorage.getItem("menolista"))).map((meno : Meno) => {
            return {
              ...meno,
              pvm : new Date(meno.pvm)
            }
          }))
        }

        if (localStorage.getItem("tulolista")) {
         
          setTulot(JSON.parse(String(localStorage.getItem("tulolista"))).map((tulo : Tulo) => {
            return {
              ...tulo,
              pvm : new Date(tulo.pvm)
            }
          }))
        }
  
      }
  
      return () => {
        kaynnistetty.current = true;
      }
  
    }, []);
  

    useEffect(() => {
  
      localStorage.setItem("menolista", JSON.stringify(menot));
      localStorage.setItem("tulolista", JSON.stringify(tulot));
  
    }, [menot, tulot]);
 
  return (

    <Container maxWidth='sm'>
      <Header/>
      

      <Routes>
        <Route path="/lisaaTulo" element={<LisaaTulo tulot={tulot} setTulot={setTulot}/>}/>
        <Route path="/" element={<Aloitus menot={menot}/>}/>
        <Route path="/muokkaaMeno/:id" element={<MuokkaaMeno menot={menot} setMenot={setMenot} menoKategoriat={menoKategoriat}/>}/>
        <Route path="/poistaMeno/:id" element={<PoistaMeno menot={menot} setMenot={setMenot}/>}/>
        <Route path="/muokkaaTulo/:id" element={<MuokkaaTulo tulot={tulot} setTulot={setTulot}/>}/>
        <Route path="/poistaTulo/:id" element={<PoistaTulo tulot={tulot} setTulot={setTulot}/>}/>
        <Route path="/yhteenveto" element={<Yhteenveto tulot={tulot} setTulot={setTulot} menot={menot} setMenot={setMenot} menoKategoriat={menoKategoriat}/>}/>
        <Route path="lisaaMeno" element={<LisaaMeno menot={menot} setMenot={setMenot} menoKategoriat={menoKategoriat}/>}/>
      </Routes>

    </Container>
  );
}

export default App;
