import { Button, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent, TextField, Typography } from '@mui/material';
import React, { useEffect, useRef, useState } from 'react';
import { LocalizationProvider, DatePicker } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fi } from 'date-fns/locale';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';
import { v4 as uuid4 } from 'uuid';
import { format } from 'date-fns';

interface Props {
  menot : Meno[]
  setMenot : (arg0 : Meno[]) => void
  menoKategoriat : string[];
}


const MuokkaaMeno : React.FC<Props> = ( {menot, setMenot, menoKategoriat} ) : React.ReactElement => {

  const muokattuMenoRef : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();

  const navigate : NavigateFunction = useNavigate();

  const [aika, setAika] = useState<Date>(new Date());
  const [kateg, setKateg] = useState<string>("")
  const [katSyotetty, setKatSyotetty] = useState<boolean>(false);
  const [summaOk, setSummaOk] = useState<boolean>();
  const [uusiMeno, setUusiMeno] = useState<string>("")
  const [tiedotOk, setTiedotOk] = useState<boolean>(false)
  
  const { id } = useParams<any>();

  const muokattavaMeno : Meno | undefined = menot.find((meno: Meno) => {
        return meno.id === id;
        });

    useEffect( () : void => {
    
    if (Number.isInteger(Number(uusiMeno)) === false || (Number(uusiMeno)) < 1 ) {
        setSummaOk(false)
        } else {
            setSummaOk(true)
            }
    
            if ( kateg === "" ) {
    setKatSyotetty(false)
    } else {
        setKatSyotetty(true)
    }

    if(summaOk === false || katSyotetty === false) {
        setTiedotOk(false)
    } else {
        setTiedotOk(true)
    }

    }, [uusiMeno, kateg, summaOk, katSyotetty]);  

  const vahvistaMuokkaus = () : void => {

    let apuMeno : Meno = {
      id : uuid4(),
      pvm : aika,
      kat: kateg || "Tuntematon",
      eur : Number(muokattuMenoRef.current!.value) || 0
    }

    setMenot([...menot.filter((meno : Meno) => meno.id !== id), apuMeno]);
    
    navigate("/");
    
  }

  return (
    <>

    <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}> 

    <Typography 
        
          variant='h6' 
          sx={{marginTop: "20px", marginBottom: "10px", marginLeft: "3px"}}
          >Muokkaa kulua</Typography>
    
    <Typography sx={{marginTop: "20px"}}>Tll voit muokata syttmsi kulua. Mikli et muuta pivmr, tallentuu muokattu kulu kuluvalle pivlle.</Typography>
    
    <Typography
          sx={{marginBottom : "10px", marginTop: "20px", fontStyle: "italic"}}
      >Haluatko varmasti muokata seuraavaa kulua: {muokattavaMeno!.eur} eur, {muokattavaMeno!.kat}, {format(muokattavaMeno!.pvm, "d.M.Y")}?</Typography> 
  
    <div style={{display: "flex", justifyContent: "space-between", marginTop: "20px"}}>
    <DatePicker
        sx={{marginBottom: "10px"}}
        label="Pivmr"
        value={aika}
        onChange={(uusiAika : Date | null) => setAika(uusiAika!)}         
    />
    <TextField 
        label="Summa"
        placeholder={String(muokattavaMeno!.eur)}
        inputRef={muokattuMenoRef}
        variant="outlined"
        required={true}
        onChange={(e) => setUusiMeno(e.target.value)}
        value={uusiMeno}
        error={!summaOk}
        helperText={!summaOk ? "Syt kokonaisluku." : ""}
        sx={{marginBottom : "10px"}}
    />
    </div>

    <FormControl
      sx={{marginBottom: "10px", marginTop: "10px"}}
      variant="outlined" 
      fullWidth
    >
      <InputLabel
        style={{backgroundColor: "#fff",
                paddingLeft: 5,
                paddingRight: 5}}
      >Kategoria</InputLabel>

      <Select
        error={!katSyotetty}
        value={kateg}
        displayEmpty
        onChange={(e : SelectChangeEvent) => { setKateg(e.target.value) }}
      >
        {menoKategoriat.sort().map((kategoria : string, idx : number) => {
          return <MenuItem key={idx} value={kategoria}>{kategoria}</MenuItem>
        })}

    
      </Select>
    </FormControl>
   

    <Button
        sx={{marginBottom: "10px"}}
        variant="contained"
        fullWidth={true}
        disabled={!tiedotOk}
        onClick={()=> {
          vahvistaMuokkaus();
          }}
    >Vahvista muokkaus</Button>  

    <Button
        fullWidth={true}
        component={Link}
        to="/"
    >Peruuta</Button> 

      </LocalizationProvider>

    </>
  );
}

export default MuokkaaMeno;