import React from 'react'
import { Button, Typography } from '@mui/material';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';
import { format } from 'date-fns';

interface Props {
    menot : Meno[]
    setMenot : (arg0 : Meno[]) => void
}

const PoistaMeno : React.FC<Props> = ({ menot, setMenot }) : React.ReactElement => {

    const navigate : NavigateFunction = useNavigate();

    const { id } = useParams<any>();

    const poistettavaMeno : Meno| undefined = menot.find((meno: Meno) => {
        return meno.id === id;
    });

    const vahvistaPoisto = () : void => {
    
        setMenot([...menot.filter((meno : Meno) => meno.id !== id)]);

        navigate("/");
    }

    return (
    <>
        <Typography variant='h6' sx={{marginTop : "20px;", marginBottom: "10px"}}>Poiston vahvistus</Typography>

       <Typography
          sx={{marginBottom : "10px", marginTop: "20px", fontStyle: "italic"}}
      >Haluatko varmasti poistaa seuraavan kulun: {poistettavaMeno!.eur} eur, {poistettavaMeno!.kat}, {format(poistettavaMeno!.pvm, "d.M.Y")}?</Typography>
        
    <Button
        variant="contained"
        fullWidth={true}
        onClick={vahvistaPoisto}
    >Poista kulu</Button>  

    <Button
        fullWidth={true}
        component={Link}
        to="/"
    >Peruuta</Button>    
    </>
    )
}

export default PoistaMeno;