import { Button, TextField, Typography } from '@mui/material';
import React, { useEffect, useRef, useState } from 'react';
import { LocalizationProvider, DatePicker } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fi } from 'date-fns/locale';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';
import { v4 as uuid4 } from 'uuid';
import { format } from 'date-fns';

interface Props {
  tulot : Tulo[]
  setTulot : (arg0 : Tulo[]) => void
}

const MuokkaaTulo : React.FC<Props> = ( {tulot, setTulot} ) : React.ReactElement => {

  const muokattuTuloRef : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();

  const navigate : NavigateFunction = useNavigate();

  const [aika, setAika] = useState<Date>(new Date());
  const [summaOk, setSummaOk] = useState<boolean>();
  const [uusiTulo, setUusiTulo] = useState<string>("");
  
  const { id } = useParams<any>();

  const muokattavaTulo : Tulo| undefined = tulot.find((tulo: Tulo) => {
        return tulo.id === id;
        });
  
  useEffect( () : void => {

    if (Number.isInteger(Number(uusiTulo)) === false || (Number(uusiTulo)) < 100 ) {
        setSummaOk(false)
        } else {
            setSummaOk(true)
            }

    }, [uusiTulo, summaOk]); 

  const vahvistaMuokkaus = () : void => {

    let apuTulo : Tulo = {
      id : uuid4(),
      pvm : aika,
      eur : Number(muokattuTuloRef.current!.value) || 0
    }

    setTulot([...tulot.filter((tulo : Tulo) => tulo.id !== id), apuTulo]);
    
    navigate("/LisaaTulo");
    
  }

  return (
    <>

    <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}> 

    <Typography 
        
          variant='h6' 
          sx={{marginTop: "20px", marginBottom: "10px", marginLeft: "3px"}}
          >Muokkaa tuloa</Typography>
    
    <Typography sx={{marginTop: "20px"}}>Tll voit muokata syttmsi tuloa. Mikli et muuta pivmr, tallentuu muokattu tulo kuluvalle pivlle.</Typography>
    
    <Typography
          sx={{marginBottom : "10px", marginTop: "20px", fontStyle: "italic"}}
      >Haluatko varmasti muokata seuraavaa tuloa: {muokattavaTulo!.eur} eur, {format(muokattavaTulo!.pvm, "d.M.Y")}?</Typography> 
   
  
    <TextField
        label="Summa "
        placeholder={String(muokattavaTulo!.eur)}
        inputRef={muokattuTuloRef}
        variant="outlined"
        fullWidth={true}
        onChange={(e) => setUusiTulo(e.target.value)}
        error={!summaOk}
        helperText="Syt kokonaisluku. Minimi 100."
        sx={{marginBottom : "10px"}}
    />

    <DatePicker
        sx={{marginBottom: "10px"}}
        label="Pivmr"
        value={aika}
        onChange={(uusiAika : Date | null) => setAika(uusiAika!)}         
    />

    <Button
        sx={{marginBottom: "10px"}}
        variant="contained"
        fullWidth={true}
        disabled={!summaOk}
        onClick={()=> {
          vahvistaMuokkaus();
          }}
    >Vahvista muokkaus</Button>  

    <Button
        fullWidth={true}
        component={Link}
        to="/"
    >Peruuta</Button> 

      </LocalizationProvider>

    </>
  );
}

export default MuokkaaTulo;